<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="addGrade">添加年级</el-button>
		</div>

		<el-dialog title="添加年级" :visible.sync="dialogGrade" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="form" label-width="80px">
					<el-form-item label="年级">
						<el-input v-model="form.title" placeholder="例:一年级"></el-input>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newGrade">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>

		<el-table :data="tableDate" ref="multipleTable">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="年级ID" align="center"></el-table-column>
			<el-table-column prop="title" label="年级名称" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button v-if="hideDel" size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog :visible.sync="dialogDel" title="删除年级" width="20%" align="center" :close-on-click-modal="false">
			<div style="font-size: 20px; margin-bottom: 30px;">是否删除该年级</div>
			<span>
				<el-button type="primary" @click="toDel">删除</el-button>
				<el-button type="danger" @click="dialogGrade = false">取消</el-button>
			</span>
		</el-dialog>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'

	export default {
		name: 'gradems',
		data() {
			return {
				dialogGrade: false,
				form: {
					title: ''
				},
				tableDate: [{}],
				classList: [],
				dialogDel: false,
				grade_id: '',
				currentPage: 1,
				totalPage: 0,
				
				permissions: localStorage.getItem('permissions'),
				role: localStorage.getItem('role'),
				hideDel: false // 是否可以使用删除按钮
			}
		},
		mounted() {
			this.getGrade();
		},
		methods: {
			// 获取年级列表
			getGrade() {
				var self = this;
				if(self.permissions.indexOf('gradeGet') > -1 ||  self.role === 'super') {
					if(self.permissions.indexOf('gradeDel') > -1 ||  self.role === 'super') {
						self.hideDel = true;
					}
					API.grades(self.currentPage).then(res => {
						self.tableDate = res.data;
						self.totalPage = res.total;
					})
				} else {
					self.$message.warning('无权限查看');
				}
			},
			// 增加年级
			addGrade() {
				var self = this;
				if(self.permissions.indexOf('gradeAdd') > -1 ||  self.role === 'super') {
					self.dialogGrade = true;
				} else {
					self.$message.warning('无此权限');
				}
			},
			newGrade() {
				var self = this;
				API.grade(self.form).then(res => {
					self.$message.success('提交成功')
					self.dialogGrade = false;
					self.getGrade();
					self.form = {};
					self.currentPage = 1
				})
			},
			// 操作
			handleDel(index, row) {
				var self = this;
				self.dialogDel = true;
				self.grade_id = row.id;
			},
			toDel() {
				var self = this;
				API.delGrade(self.grade_id).then(res => {
					self.$message.success('删除成功')
					self.dialogDel = false;
					self.getGrade();
					self.currentPage = 1
				})
			},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getGrade();
			},
		}
	}
</script>

