<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="addClass">添加班级</el-button>
		</div>
		<div class="btn">
			<el-select v-model="grade" placeholder="请选择年级" @change="gradeChange">
				<el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id">
				</el-option>
			</el-select>
		</div>
		<el-dialog title="添加年级" :visible.sync="dialogClass">
			<div class="box">
				<el-form :model="form" label-width="80px">
					<el-form-item label="选择年级">
						<el-select v-model="form.grade_id" placeholder="请选择年级">
							<el-option v-for="item in formGradeList" :key="item.id" :label="item.title" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="班级名称">
						<el-input v-model="form.title"></el-input>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newClass">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>

		<el-table :data="tableDate" ref="multipleTable">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="班级ID" align="center" width="150px"></el-table-column>
			<el-table-column prop="grade" label="年级" align="center" width="150px"></el-table-column>
			<el-table-column prop="title" label="班级" align="center" width="150px"></el-table-column>
			<el-table-column prop="teacher.name" label="班主任" align="center" width="150px"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini" type="success" @click="handleStudent(scope.$index,scope.row)">查看学生</el-button>
					<el-button size="mini" type="success" @click="handleTeacher(scope.$index,scope.row)">教师列表</el-button>
					<el-button v-if="hideDel" size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!-- 更换人脸图片 -->
		<el-dialog title="更换人脸" :visible.sync="dialogFace" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="familyForm">
					<el-form-item label="选择家长">
						<el-select v-model="familyForm.user_id" filterable placeholder="请选择家长">
							<el-option v-for="item in familyList" :key="item.id" :label="item.nickname" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="更换人脸图片">
						<el-upload action="https://upload-z2.qiniup.com" ref="upload" :limit="1" :before-upload="beforeAvatarUpload"
						 :on-success="handleAvatarSuccess" :on-remove="handleRemove" :on-exceed="handleExceed" :data="imgData">
							<el-button size="small" type="primary">选择图片</el-button>
						</el-upload>
						<div class="up-img">
							<img class="pic-box" :src="familyForm.href" v-if="familyForm.href">
						</div>
					</el-form-item>
				</el-form>
			</div>
		</el-dialog>

		<!-- 查看学生 -->
		<el-dialog title="查看学生" :visible.sync="dialogStudent" width="80%" :close-on-click-modal="false">
			<div class="box">
				<div style="margin-bottom: 20px;">
					<span>班级总人数：{{classTotal}}</span>
				</div>
				<el-table :data="studentDate">
					<el-table-column prop="number" label="学号" align="center"></el-table-column>
					<el-table-column prop="name" label="姓名" align="center"></el-table-column>
					<el-table-column prop="sex" label="性别" align="center">
						<template slot-scope="scope"><span v-text="scope.row.sex == '1' ? '男' : '女'"></span></template>
					</el-table-column>
					<el-table-column prop="face_image" label="人脸头像" align="center">
						<template slot-scope="scope">
							<img :src="scope.row.face_image" alt="" style="max-width:180px;max-height:80px;">
						</template>
					</el-table-column>
					<el-table-column label="操作" align="center">
						<template slot-scope="scope">
							<el-button size="mini" type="success" @click="handleFace(scope.$index,scope.row)">更换人脸照片</el-button>
						</template>
					</el-table-column>
				</el-table>

				<div class="block">
					<el-pagination @current-change="handleStudentCurrentChange" :current-page.sync="currentStudentPage" :page-size="10"
					 layout="prev, pager, next, jumper" :total="StudentTotalPage">
					</el-pagination>
				</div>
			</div>
		</el-dialog>
		<!-- 查看教师 -->
		<el-dialog title="查看教师" :visible.sync="dialogTeacher">
			<div class="box">
				<el-table :data="TeacherDate" ref="multipleTable">
					<el-table-column prop="name" label="姓名" align="center"></el-table-column>
					<el-table-column prop="phone" label="手机号" align="center"></el-table-column>
					<el-table-column prop="href" label="人脸图像" align="center">
						<template slot-scope="scope">
							<img :src="scope.row.href" alt="" style="max-width:180px;max-height:80px;">
						</template>
					</el-table-column>
					<!-- <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                            <el-button size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除该老师身份</el-button>
                        </template>
                    </el-table-column> -->
				</el-table>
			</div>
		</el-dialog>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	import md5 from 'blueimp-md5'
	import axios from 'axios'

	export default {
		name: 'classms',
		data() {
			return {
				dialogClass: false,
				form: {
					grade_id: '',
					title: ''
				},
				formGradeList: [],
				grade: '',
				gradeList: [{
					value: 0,
					title: '全部'
				}],

				// 操作
				dialogStudent: false,
				studentDate: [{}],
				id: '', // 班级id
				classTotal: '', // 班级总人数
				dialogTeacher: false,
				TeacherDate: [],
				dialogFace: false,
				familyList: [],
				// nickname: '',
				imgData: {
					key: '',
					token: ''
				},
				fileName: '',
				suffix: '',
				qiniuaddr: "https://tu.fengniaotuangou.cn", // 七牛云图片外链地址
				familyForm: {
					href: '',
					user_id: '',
					student_id: ''
				},
				tableDate: [{}],
				currentPage: 1,
				totalPage: 0,
				currentStudentPage: 1,
				StudentTotalPage: 0,
				
				permissions: localStorage.getItem('permissions'),
				role: localStorage.getItem('role'),
				hideDel: false // 是否可以使用删除按钮
			}
		},
		mounted() {
			this.getGrade();
			this.getClass();
		},
		methods: {
			// 获取年级列表
			getGrade() {
				var self = this;
				if(self.permissions.indexOf('gradeGet') > -1 || self.role === 'super') {
					API.grades(self.currentPage).then(res => {
						res.data.forEach(item => {
							self.gradeList.push(item)
						})
					})
				} else {
					self.$message.warning('无此权限');
				}
				
			},
			addClass() {
				var self = this;
				self.dialogClass = true;
				API.grades(self.currentPage).then(res => {
					self.formGradeList = res.data;
				})
			},
			// 获取班级列表
			getClass() {
				var self = this;
				API.classes(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newClass() {
				var self = this;
				self.gradeList.forEach(item => {
					if (self.grade === item.id) {
						self.form.grade_id = item.id
					}
				})
				API._class(self.form).then(response => {
					self.$message.success('提交成功')
					self.dialogClass = false;
					self.getClass();
					self.grade = '';
					self.form = {};
					self.currentPage = 1;
				})
			},

			// 根据年级筛选
			gradeChange() {
				var self = this;
				API.classes(self.currentPage, self.grade).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			// 操作
			handleStudent(index, row) {
				var self = this
				self.dialogStudent = true;
				self.id = row.id;
				API.students(self.currentPage, self.id).then(res => {
					self.studentDate = res.data;
					self.classTotal = res.total;
				})
			},
			handleTeacher(index, row) {
				var self = this
				self.dialogTeacher = true;
				self.TeacherDate = [row.teacher]
			},
			handleDel() {},
			// 更换人脸
			handleFace(index, row) {
				var self = this
				self.dialogFace = true;
				self.familyForm.student_id = row.id;
				API.bindFamily(self.currentPage, 10000).then(res => {
					self.familyList = res.data
				})
			},

			// 人脸信息
			handleRemove(file, fileList) {},
			beforeAvatarUpload(file) {
				var self = this;
				self.fileName = md5(file.name);
				self.suffix = file.name.substring(file.name.lastIndexOf('.') + 1);
				self.imgData.key = `tmp_${self.fileName}.${self.suffix}`
			},
			handleAvatarSuccess(res, file) {
				var self = this;
				// self.imageUrl = URL.createObjectURL(file.raw);
				file.url = `${self.qiniuaddr}/${res.key}`;
				self.familyForm.href = file.url;
				API.updateFace(self.familyForm).then(res => {
					self.$message.success('上传成功');
					self.currentPage = 1;
					self.getStudent();
					self.$refs.upload.clearFiles()
					self.familyForm.href = '';
					self.imgData.key = '';
					self.familyForm.user_id = '';
					self.dialogFace = false;
				})
			},
			handleExceed(file, fileList) { //图片上传超过数量限制
				var self = this;
				self.$message.error('上传图片不能超过1张!重新上传');
				self.$refs.upload.clearFiles()
				self.familyForm.href = '';
				self.imgData.key = '';
				self.familyForm.user_id = '';
			},

			getQiniuToken() {
				var self = this;
				axios.get('https://api.fengniaotuangou.cn/api/upload/token').then(res => {
					self.imgData.token = res.data.uptoken;
				})
			},




			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getClass();
			},
			handleStudentCurrentChange(val) {
				var self = this;
				API.students(self.currentPage, self.id).then(res => {
					self.studentDate = res.data;
					self.classTotal = res.total;
				})
			}
		}
	}
</script>

